<template>
  <div class="notification" :class="notificationClass">
    <div class="message">{{ message }}</div>
    <div class="button-container">
      <button class="close-button" @click="handleNotificationButtonClick">
        Confirm
      </button>
    </div>
  </div>
  <div class="record">
    <div id="id"></div>
    <div id="score"></div>
    <div id="count"></div>
    <div id="level"></div>
    <div id="accelerationX"></div>
    <div id="accelerationY"></div>
    <div id="accelerationZ"></div>
  </div>
</template>

<script lang="js">
import { defineComponent } from "vue";
import { csmapi } from '/src/csmapi.js';

export default defineComponent({
  props: {
    uuid: {
      type: Number,
      required: true,
      default: 1,
    },
    name: {
      type: String,
      required: true,
      default: "1",
    },
  },
  data() {
    return {
      message: "Press the buttom to confirm access to the accelerometer.",
      isActive: true,
      timeout: 0,
      acceleration: { x: 0, y: 0, z: 0, },
      cnt: 0,
      notshake: 1,
      level: 1,
      mylevelinterval: 0,
      mycntinterval: 0,
    };
  },
  computed: {
    notificationClass() {
      return this.isActive ? "active" : "";
    },
  },
  mounted() {
    this.timeout = setTimeout(() => {
      this.hideNotification();
    }, 10000);
    this.mylevelinterval = setInterval(this.updatelevel, 500);
    this.mycntinterval = setInterval(this.updatecnt, 500);
  },
  beforeUnmount() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
  },
  methods: {
    hideNotification() {
      this.isActive = false;
    },
    handleNotificationButtonClick() {
      this.requestDeviceMotionPermission()
        .then((permissionGranted) => {
          if (permissionGranted) {
            console.log("permissionGranted");
            this.hideNotification();
            this.pushScore();
          } else {
            alert("未獲得加速度感測器權限");
          }
        })
        .catch(console.error);
    },
    requestDeviceMotionPermission() {
      return new Promise((resolve, reject) => {
        if (typeof DeviceMotionEvent.requestPermission === "function") {
          DeviceMotionEvent.requestPermission()
            .then((permissionState) => {
              if (permissionState === "granted") {
                window.addEventListener("devicemotion", this.handleDeviceMotion.bind(this));
                this.updateInterval = setInterval(this.updateScore, 100);
                resolve(true);
              } else {
                resolve(false);
              }
            })
            .catch(reject);
        } else {
          window.addEventListener("devicemotion", this.handleDeviceMotion);
          this.updateInterval = setInterval(this.updateScore, 100);
          resolve(true);
        }
      });
    },
    handleDeviceMotion(event) {
        this.acceleration.x = event.accelerationIncludingGravity?.x || 0;
        this.acceleration.y = event.accelerationIncludingGravity?.y || 0;
        this.acceleration.z = event.accelerationIncludingGravity?.z || 0;

        var accelerationX = document.getElementById("accelerationX"),
            accelerationY = document.getElementById("accelerationY"),
            accelerationZ = document.getElementById("accelerationZ");

        accelerationX.innerHTML = this.acceleration.x.toFixed(2);
        accelerationY.innerHTML = this.acceleration.y.toFixed(2);
        accelerationZ.innerHTML = this.acceleration.z.toFixed(2);

        this.$emit("update-level", this.level);
        this.$emit("update-cnt", this.cnt);
    },
    updateScore() {
      const scoreElement = document.getElementById("score");
        const score = Math.sqrt(
            Math.pow(this.acceleration.x, 2) +
            Math.pow(this.acceleration.y, 2) +
            Math.pow(this.acceleration.z, 2)
        );

        scoreElement.innerHTML = score.toFixed(2);

        const cntElement = document.getElementById("count");

        if (score >= 15 && this.cnt < 15) {
            this.cnt += 1;
            this.notshake = 0;
        }
        cntElement.innerHTML = this.cnt.toFixed(2);
    },
    updatecnt() {
        if (this.notshake && this.cnt > 0) {
          this.cnt -= 1;
        }
        this.notshake = 1;
    },
    updatelevel() {
        const levelElement = document.getElementById("level");
        if (this.cnt >= 10 && this.level < 5) {
          this.level += 1;
            if (this.level == 5) {
            clearInterval(this.mylevelinterval);
            this.mylevelinterval = setInterval(this.updatelevel, 10000);
            } else {
            clearInterval(this.mylevelinterval);
            this.mylevelinterval = setInterval(this.updatelevel, 5000);
            }
            this.cnt = 0;
        } else if (this.cnt <= 10 && this.level > 1) {
            this.level -= 1;
            clearInterval(this.mylevelinterval);
            this.mylevelinterval = setInterval(this.updatelevel, 5000);
        }
        levelElement.innerHTML = this.level?.toFixed(2) || 1;
    },
    pushScore() {
      csmapi.set_endpoint("https://6.iottalk.tw");

      const mac_addr = "calltt";
      const idf_name = "callt";
      const data = [this.uuid, this.name, this.level];

      csmapi.push(mac_addr, idf_name, data, (data, error) => {
        if (error) {
          console.error("Failed to push score:", error);
          return;
        }
        setTimeout(this.pushScore, 5000);
      });
    },
  },
});
</script>

<style scoped>
.notification {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #333;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  z-index: 9999;
  opacity: 0;
  transition: opacity 0.3s;
  width: 400px;
  height: 100px;
  font-size: 32px;
}

.notification.active {
  opacity: 1;
}

.message {
  margin-bottom: 10px;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}

.close-button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 32px;
}
.record {
  display: none;
}
</style>
