
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    score: {
      type: Number,
      required: true,
    },
  },
  computed: {
    scoreString(): string {
      return this.score.toString().padStart(5, "0");
    },
  },
  data() {
    return {
      pictureSrc: require("@/assets/score.png"),
    };
  },
});
