
import { defineComponent } from "vue";
import CheerStick from "@/components/CheerStick.vue";
type CheerstickProps = {
  type: string;
  level: number;
  x: number;
  y: number;
};
export default defineComponent({
  components: {
    CheerStick,
  },
  props: {
    users: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      cheersticks: [] as CheerstickProps[],
      numCheersticks: 0,
      maxCheersticks: 30,
    };
  },
  mounted() {
    setInterval(() => {
      this.adjustCheersticks();
    }, 1000);
  },
  watch: {
    users(newValue) {
      if (newValue != this.numCheersticks) this.adjustCheersticks();
    },
  },
  methods: {
    adjustCheersticks() {
      if (this.numCheersticks < this.users) {
        const cheersticksToAdd = this.users - this.numCheersticks;
        for (let i = 0; i < cheersticksToAdd; i++) {
          this.addCheerstick();
        }
      } else if (this.numCheersticks > this.users) {
        const cheersticksToRemove = this.numCheersticks - this.users;
        for (let i = 0; i < cheersticksToRemove; i++) {
          this.removeCheerstick();
        }
      }
    },
    addCheerstick() {
      const dir = this.numCheersticks % 2 == 0 ? 1 : -1;
      const pos =
        this.numCheersticks % 2 == 0
          ? window.innerWidth * 0.0
          : window.innerWidth * 0.85;
      const interval = 38;
      const ranInt = Math.random() * 2 - 1;
      const cspeed = Math.floor(Math.random() * 4) + 1;
      const sspeed = Math.floor(Math.random() * 4) + 1;
      const xpos = Math.floor(
        pos + dir * Math.floor(this.numCheersticks / 2) * interval + ranInt * 5
      );
      const ypos = Math.floor(
        55 + this.numCheersticks * 1.6 + Math.random() * 5
      );
      const cheerstick = {
        type: "sword",
        level: cspeed,
        x: xpos,
        y: ypos,
      } as CheerstickProps;
      const shadow = {
        type: "sshadow",
        level: sspeed,
        x: xpos + ranInt * 30,
        y: ypos,
      } as CheerstickProps;
      if (this.numCheersticks % 5 == 0) {
        cheerstick.type = "stick";
        shadow.type = "bshadow";
      }
      this.numCheersticks++;
      this.cheersticks.push(shadow);
      this.cheersticks.push(cheerstick);
    },
    removeCheerstick() {
      this.cheersticks.pop();
      this.cheersticks.pop();
    },
  },
});
