<template>
  <div class="notification" :class="notificationClass">
    <div class="message">{{ message }}</div>
    <div class="button-container">
      <!-- <button class="close-button" @click="handleLogin">Login</button> -->
      <!-- <button
        class="close-button"
        @click="handleSignIn"
        :disabled="!Vue3GoogleOauth.isAuthorized"
      >
        Sign In
      </button> -->
      <SignInButton class="close-button" @signin-click="handleLogin" />
    </div>
  </div>
</template>

<script lang="js">
import { defineComponent } from "vue";
import SignInButton from "@/components/GoogleLoginButton.vue";

export default defineComponent({
  components: {
    SignInButton,
  },
  data() {
  return {
    message: "",
    isActive: true,
    timeout: null,
  };
},
  props: {
    emitLogin: {
      type: Function,
      required: true,
    },
  },
  computed: {
    notificationClass(){
      return this.isActive ? "active" : "";
    },
  },
  methods: {
    hideNotification() {
      this.isActive = false;
    },
    handleLogin() {
      this.hideNotification();
      this.emitLogin();
    },
  },
});
</script>

