<template>
  <div>
    <GoogleLogin :callback="callback" @click="handleClick" prompt auto-login />
  </div>
</template>

<script lang="js">
import { ref } from "vue";
import { googleTokenLogin, googleAuthCodeLogin, decodeCredential } from "vue3-google-login";
import { defineComponent} from "vue";

const GOOGLE_CLIENT_ID =
  "67619821297-nuqc4k56qbhemuho2fttb7f3re09r8vd.apps.googleusercontent.com";

const data = ref();

export default defineComponent({
    methods: {
      handleClick() {
        this.$emit("signin-click");
      },
        handleGoogleAccessTokenLogin() {
            googleTokenLogin({
                clientId: GOOGLE_CLIENT_ID,
            }).then((response) => {
                data.value = response;
            });
        },

        handleGoogleAuthCodeLogin() {
            googleAuthCodeLogin({
                clientId: GOOGLE_CLIENT_ID,
            }).then((response) => {
                data.value = response;
            });
        },
        callback(response) {
        // decodeCredential will retrive the JWT payload from the credential
        const userData = decodeCredential(response.credential);
        console.log("Handle the userData", userData);
        this.$emit("user-info-received", userData);
        },
    },
});
</script>

<style scoped>
p {
  margin-top: 12px;
  word-break: break-all;
}
</style>
