<template>
  <div class="window">
    <Marquee :userList="userList"></Marquee>
    <CheerSticks :users="users" />
    <Users :gifIdx="UserIdx" :users="users" />
    <Score :score="score" />
  </div>
</template>

<script lang="js">
import { defineComponent } from "vue";
import CheerSticks from "@/components/ReceiverSticks.vue";
import Users from "@/components/ReceiverUsers.vue";
import Score from "@/components/ReceiverScore.vue";
import Marquee from "@/components/ReceiverMarquee.vue";
import { csmapi } from "../csmapi.js";

export default defineComponent({
  components: {
    CheerSticks,
    Users,
    Score,
    Marquee,
  },
  data() {
    return {
      level: 1,
      UserIdx: 1,
      score: 1805,
      users: 0,
      dataList: [],
    };
  },
  methods: {
    pullData() {
      csmapi.set_endpoint("https://6.iottalk.tw");

      const mac_addr = "callrr";
      const odf_name = "callr";
      csmapi.pull(mac_addr, odf_name, (data, error) => {
        if (error) {
          console.error("Failed to pull data:", error);
          return;
        }

        this.processData(data);
      });
    },
    processData(data) {
      // Iterate over the received data
      data.forEach((item) => {
        console.log(item[1]);
        const [id, name, level] = item[1];
        // Check if the id already exists in the dataList
        const existingData = this.dataList.find((Data) => Data.id === id);

        if (existingData) {
          // Update the existing data
          existingData.name = name;
          existingData.level = level;
        } else {
          // Add new data to the dataList
          this.dataList.push({ id, name, level });
        }
      });
      // Calculate the sum
      //console.log(data);
      const sum = this.dataList.reduce((acc, item) => acc + item.level, 0);
      this.users = this.dataList.length;
      this.userList = this.dataList.map((item) => item.name);
      this.handleScoreChange(sum);
      setTimeout(this.pullData, 200);
    },
    handleScoreChange(newScore) {
      this.score = newScore;
      console.log("Score changed:", this.score);
    },
    // handleUsersChange(newUsers: number) {
    //   this.users = newUsers;
    //   console.log("Users changed:", this.users);
    // },
  },
  mounted() {
    this.pullData();
    setInterval(() => {
      this.UserIdx++;
      if (this.UserIdx > 9) {
        this.UserIdx = 1;
      }
    }, 100);
  },
});
</script>

<style scoped>
.window {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: black;
}
</style>
